.best_movie {
  flex: 1.2;
  display: flex;
}

.top_movie {
  display: flex;
  flex-direction: column;
}

.title_list {
  display: flex;
  padding: 2rem 1rem;
  justify-content: space-between;
}

.title {
  display: flex;
  justify-content: flex-start;
  font-size: 25px;
}

.movie_description {
  position: relative;
}

.description {
  position: absolute;
  background: black;
  width: 55%;
  bottom: 1rem;
  right: 0;
  opacity: 0.7;
  padding: 1rem;
  z-index: 1;
}


.title_movie {
  text-align: center;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
}
.best_title {
  font-size: 30px;
  font-weight: 900;
}


.info_movie {
  display: flex;
  justify-content: center;
  align-items: center;
}


.best_description {
  margin: 0.5rem;
}

.best_more {
  color: rgb(255, 196, 0);
}


.best_movie_img {
  width: 100%;
  height: 100%;
  animation: fadeImage 2s forwards;
  -webkit-animation: fadeImage 2s forwards;
  -o-animation: fadeImage 2s forwards;
  -moz-animation: fadeImage 2s forwards;
}


@keyframes fadeImage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}